@value tableWidth: min(170vw, 170vh);
@value tableHeight: tableWidth;

@value pageWidth: 35%;
@value pageHeight: 45%;

@value bookWidth: pageWidth;
@value bookHeight: pageHeight;
@value bookTop: 5%;

@value cardSlotWidth: 60%;
@value cardSlotHeight: 10%;

@value tableColor: #763F13;
@value tableColorDark: #221100;

.room {
  position: absolute;
  perspective: calc(tableWidth * 3);
  perspective-origin: 50% 50%;
  width: 100%;
  height: 100%;
}

.container {
  position: absolute;
  left: calc((100vw - tableWidth)/2);
  bottom: 0;
  width: tableWidth;
  height: tableHeight;
}

.table {
  background: tableColor;
  position: absolute;
  width: 100%;
  height: 100%;
  transform-origin: calc(tableWidth/2) tableHeight;
  transition: transform 1s ease-in-out, background-color 1s ease-in-out;
}

.table.topDown {
  background: tableColorDark;
  transform:
      rotateX(20deg)
      translate(0, calc(bookHeight + bookTop))
      translateZ(calc(-1 * tableWidth));
}

.table.topDownBookCentered {
  // TODO gradients aren't transitionable, but an overlay with opacity is...
  background: linear-gradient(tableColorDark, tableColor);
  // TODO this math is wrong to centre the book
  transform:
      rotateX(30deg)
      translate(calc(-1 * (bookWidth / 2)), calc(bookHeight + bookTop))
      translateZ(0);
}

.table.tilted {
  // TODO gradients aren't transitionable, but an overlay with opacity is...
  background: linear-gradient(tableColorDark, tableColor);
  transform: rotateX(70deg) translate(0, 0) translateZ(0);
}

.book-slot {
  position: absolute;
  top: bookTop;
  left: 50%;
  width: bookWidth;
  height: bookHeight;
  background-color: brown;
}

.card-slot {
  position: absolute;
  top: calc(bookTop + bookHeight + 10%);
  left: calc((100% - cardSlotWidth) / 2);
  width: cardSlotWidth;
  height: cardSlotHeight;
  background-color: brown;

}